.game-page {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-areas:
    "playersInfo gameInfo hint"
    "playersInfo game notepad"
    "message game notepad"
    "bioms game notepad"
    "legend game notepad";
  grid-template-rows: auto 1fr auto auto auto;
  grid-template-columns: 300px 1fr 300px;

  box-sizing: border-box;
  padding: 10px;
  grid-gap: 10px;
}

.block-wrapper {
  border: 2px solid var(--block-border);
  position: relative;
  padding: 10px;
}
.block-wrapper h1 {
  background-color: var(--background);
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 5px;
  transform: translateY(-60%);
  padding: 2px;
  line-height: 100%;
}

.icon {
  cursor: pointer;
  width: 40px;
  height: auto;
}

.action-menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  box-shadow: 8px 8px 5px -5px rgba(34, 60, 80, 0.6);
}
.action-menu button {
  border: none;
}
.action-menu button:hover {
  text-decoration: underline;
}
