.game-info {
  grid-area: gameInfo;
  display: flex;
  align-items: center;
}

.game-info .block-wrapper {
  flex-grow: 1;
  display: flex;
  align-items: center;
  height: 100%;
  box-sizing: border-box;
  justify-content: center;
}

.buttons {
  padding: 2px;
  display: flex;
  align-items: center;
  column-gap: 20px;
}
