.bioms-wrapper {
  grid-area: bioms;
}

.legen-list {
  display: flex;
  justify-content: space-between;
}

.legend-item {
  position: relative;
  padding-bottom: 5px;
}

.legend-image {
  width: 25px;
  height: 25px;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
}
.legend-text {
  position: absolute;
  font-size: 10px;
  transform-origin: 50% 50%;
  left: 50%;
  bottom: -7px;
  transform: translateX(-50%);
}

.legend-image.star-legend {
  background-color: var(--text-color);
  clip-path: polygon(
    50% 0%,
    70% 25%,
    98% 35%,
    80% 60%,
    79% 91%,
    50% 80%,
    21% 91%,
    20% 60%,
    2% 35%,
    30% 25%
  );
}
.legend-image.triangle {
  background-color: var(--text-color);
  clip-path: polygon(50% 0%, 100% 100%, 0% 100%);
}
