.right-wrapper {
  grid-area: notepad;
  display: grid;
  grid-template-rows: minmax(0, 1fr) minmax(0, 1fr);
  flex-direction: column;
  row-gap: 10px;
}

.log-wrapper {
  display: flex;
}

.notepad {
  resize: none;
  width: 100%;
  height: 100%;
  padding: 5px;
  box-sizing: border-box;
  background: var(--notepad);
  color: var(--text-color);
}
