.log {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  flex: 1 0 auto;
  align-items: flex-start;
}
.log::-webkit-scrollbar {
  width: 5px;
}
.log::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: #bbb;
}

.log-item {
  display: flex;
  align-items: center;
  column-gap: 5px;
  padding: 2px 0px;
}
.log-answer {
  width: 10px;
  height: 10px;
  border: 1px solid black;
}

.log-answer.circle {
  border-radius: 10px;
}
