@import url("https://fonts.googleapis.com/css2?family=Golos+Text&display=swap");

:root {
  --water: #6abbcd;
  --rock: #848482;
  --forest: #6bb260;
  --swamp: #bb4d98;
  --desert: #e0c662;
  --jaguar: #d02030;
  --bear: #363237;

  --background: #e9e9e9;
  --text-color: #363237;

  --notepad: #deddd9;

  --player-red: red;
  --player-green: green;
  --player-blue: blue;
  --player-brown: brown;
  --player-purple: purple;
  /* --player-red: #be3a23;
  --player-green: #01684d;
  --player-blue: #0188b5;
  --player-brown: #6f462b;
  --player-purple: #756899; */

  --building-white: white;
  --building-blue: blue;
  --building-green: green;
  --building-black: black;

  --block-border: black;
}

.dark {
  --background: #363237;
  --text-color: #f2f2f2;

  --notepad: #29252a;

  --block-border: black;
}

* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: "Golos Text";
  color: var(--text-color);
}

input {
  border: 1px solid black;
  padding: 2px;
}

input:focus {
  outline: none;
  border: 1px solid lightseagreen;
}

button {
  background: var(--background);
  color: var(--text-color);
  border-radius: 0px;
  border: none;
  padding: 4px;
  border: 1px solid black;
}
button:hover {
  cursor: pointer;
}
button:disabled {
  opacity: 0.6;
  pointer-events: none;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background);
}

.map-wrapper {
  flex-grow: 1;
  overflow: hidden;
  grid-area: game;
}

.items {
  display: flex;
  column-gap: 5px;
  margin-bottom: 5px;
}

.form-page {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  position: relative;
  row-gap: 5px;
}

.modal-form-page {
  box-shadow: none;
  border-radius: unset;
}

.form-page-buttons {
  display: flex;
  justify-content: space-between;
}

.ask-modal {
  background: var(--background);
  padding: 10px;
  padding-top: 2px;
  padding-bottom: 4px;
}

.ask-modal .list-item {
  text-align: center;
}

.ask-modal .list-item:hover {
  background: lightblue;
}
.player-red {
  color: var(--player-red);
}
.player-green {
  color: var(--player-green);
}
.player-blue {
  color: var(--player-blue);
}
.player-brown {
  color: var(--player-brown);
}
.player-purple {
  color: var(--player-purple);
}

.water {
  background: var(--water);
}
.rock {
  background: var(--rock);
}
.forest {
  background: var(--forest);
}
.swamp {
  background: var(--swamp);
}
.desert {
  background: var(--desert);
}
.jaguar {
  border: 1px solid var(--jaguar);
}
.bear {
  border: 1px solid var(--bear);
}
