.players-info-wrapper {
  grid-area: playersInfo;
}

.players-list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.nickname-info {
  display: flex;
  justify-content: space-between;
  column-gap: 10px;
}

.nickname {
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
}

.score {
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border-radius: 5px;
}

.score.circle {
  border-radius: 100%;
}
.divine {
  height: 2px;
  background-color: rgb(218, 218, 218);
  margin: 10px 0;
}
.score-wrapper {
  display: flex;
  column-gap: 10px;
  margin-bottom: 5px;
}
